import React from "react";
import style from "./Project.module.scss"

import {ButtonComponent} from "../../common/components/buttonComponent/ButtonComponent";

type WorkPropsType={
    nameProject:string,
    shortDesc:string
    image:string
    link?:string
}


export function Project(props:WorkPropsType) {
    return (
        <div className={style.work}>
            <div   className={style.image} style={{backgroundImage:props.image}}>
               <ButtonComponent link={props.link} title={"View"}/>
            </div>
            <div className={style.description}>
                <h3 className={style.nameProject}>{props.nameProject}</h3>
                <p className={style.shortDescription}>{props.shortDesc}</p>
            </div>
        </div>


    )
}

export default Project;
