import React, {DetailedHTMLProps, FormHTMLAttributes, useRef, useState} from "react";
import style from "./Contact.module.scss"
import {TitleComponents} from "../common/components/titleComponets/TitleComponents";
import commonStyle from "../common/commonStyle/sass/common.module.scss"
import {ButtonComponent} from "../common/components/buttonComponent/ButtonComponent";
//@ts-ignore
import Fade from 'react-reveal/Fade';
import {Formik} from "formik";
import emailjs from 'emailjs-com';


export function Contact() {
    const form = useRef();
    const [show, setShow] = useState(false);

    // @ts-ignore
    return (
        <div className={commonStyle.commonBlock} style={{scrollBehavior: 'smooth'}}>
            < Fade>
                < div id={'idContact'} className={commonStyle.commonContainer} style={{textAlign: "center"}}>
                    <TitleComponents title={"Contact"}/>
                    <div className={style.innerBlock}>
                        <Formik
                            initialValues={{email: '', name: '', message: ''}}
                            validate={values => {
                                const errors = {};
                                if (!values.email) {
                                    //@ts-ignore
                                    errors.email = 'Required';
                                } else if (
                                    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                                ) {
                                    //@ts-ignore
                                    errors.email = 'Invalid email address';
                                }
                                if (!values.name) {
                                    //@ts-ignore
                                    errors.name = 'Required';
                                }
                                if (!values.message) {
                                    //@ts-ignore
                                    errors.message = 'Required';
                                }

                                return errors;
                            }
                            }
                            onReset={(values) => {
                                values.email = ""
                                values.name = ""
                                values.message = ""
                            }}
                            onSubmit={(values) => {
                                emailjs.sendForm('service_dyulr3a', 'template_nediuss', form.current!, 'user_4yBKmS2ivP299hd3kz66U')
                                    .then((result) => {
                                        alert(result.text);
                                        setShow(true)
                                    }, (error) => {
                                        alert(error.text);
                                    });

                            }}
                        >
                            {({
                                  values,
                                  errors,
                                  touched,
                                  handleChange,
                                  handleSubmit,
                                  handleReset,
                                  handleBlur,
                                  isSubmitting,
                              }) => (
                                <form
                                    // @ts-ignore
                                    ref={form} onSubmit={handleSubmit} className={style.forma} action="">
                                    <input type="name"
                                           name="name"
                                           placeholder="Enter name"
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.name}/>
                                    {errors.name ? <span
                                            style={{color: "orange"}}>{errors.name && touched.name && errors.name}</span>
                                        : values.name ? null :
                                        <span style={{color: "orange"}}>Please enter your Name.</span>}
                                    <input name={"email"} type="email"
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.email}
                                           placeholder="Enter email"/>
                                    {errors.email ? <span
                                            style={{color: "orange"}}>{errors.email && touched.email && errors.email}</span> :
                                        values.email ? null :
                                        <span style={{color: "orange"}}>Please enter your Email.</span>}
                                    <textarea
                                        placeholder="Enter text"
                                        name="message"
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.message}/>
                                    {errors.message ? <span
                                            style={{color: "orange"}}>{errors.message && touched.message && errors.message}</span> :
                                        values.message ? null :
                                        <span style={{color: "orange"}}>Please enter your Text Message.</span>}
                                    <div style={{marginTop:'5px'}}>
                                        <ButtonComponent disable={isSubmitting}
                                                         title={"SEND MESSAGE"} buttonType={"submit"}/>
                                        <ButtonComponent title={"RESET"} onClick={() => handleReset}/>
                                    </div>
                                </form>
                            )}
                        </Formik>
                        <div className={style.blockMap}>
                            <iframe
                                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d150505.20100923395!2d27.453286768317984!3d53.88476083077986!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46dbcfd35b1e6ad3%3A0xb61b853ddb570d9!2z0JzQuNC90YHQug!5e0!3m2!1sru!2sby!4v1636710848058!5m2!1sru!2sby"
                                loading="lazy"/>
                        </div>
                    </div>

                </div>
            </Fade>
        </div>
    )
}

export default Contact;


