import React, {useState} from "react";
import style from './BurgerMenu.module.scss'


type BurgerMenuProps = {
    onChange: () => void;
    flag:boolean
}
export const BurgerMenu = (props: BurgerMenuProps) => {
    return (
        <div onClick={() => props.onChange()} className={props.flag?style.burgerOpen:style.burger}>
            <div className={props.flag?style.middleLineOpen:style.middleLine}/>
        </div>
    )
}