import React, {useCallback, useEffect, useRef, useState} from "react";
import style from './Nav.module.scss';
import hello from "../../image/hello.svg"
import {ButtonComponent} from "../../common/components/buttonComponent/ButtonComponent";
import {BurgerMenu} from "../../common/components/burgerMenu/BurgerMenu";


export function Nav() {
    const [flag, setFlag] = useState<boolean>(false)

    function onFlagChange() {
        setFlag(!flag)
    }

    return (
        <div className={style.container}>
            <div className={style.wrapIcon}><a href="##"><img className={style.mainIconHi} src={hello} alt=""/></a>
            </div>
            <div className={style.nav}>
                <a href={'##'}>About</a>
                <a href={"#idSkills"}>Skills</a>
                <a href={"#idProjects"}>Projects</a>
                <a href={"#idContact"}>Contacts</a>
            </div>
            <ButtonComponent title={"DOWNLOAD CV"}/>
            <div className={style.burgerWrapper}>
                <BurgerMenu onChange={onFlagChange} flag={flag}/>
                <div onBlur={() => setFlag(false)}
                     className={flag ? style.burgerMenu : style.burgerMenuHide}>
                    <a href={'#idAbout'}>About</a>
                    <a href={"#idSkills"}>Skills</a>
                    <a href={"#idProjects"}>Projects</a>
                    <a href={"#idContact"}>Contacts</a>
                </div>
            </div>

        </div>
    )
}

export default Nav;
